import { init, parse } from "es-module-lexer";
import {
  BARE_IMPORT_RE,
  DEFAULT_EXTERSIONS,
  PRE_BUNDLE_DIR,
} from "../constants";
import { cleanUrl, isJSRequest, normalizePath } from "../utils";
// magic-string Áî®Êù•‰ΩúÂ≠óÁ¨¶‰∏≤ÁºñËæë
import MagicString from "magic-string";
import path from "path";
import { Plugin } from "../plugin";
import { ServerContext } from "../server/index";
import { pathExists } from "fs-extra";
import resolve from "resolve";
import type { PluginContext } from "rollup";

export function importAnalysisPlugin(): Plugin {
  let serverContext: ServerContext;
  return {
    name: "m-vite:import-analysis",
    configureServer(s) {
      // ‰øùÂ≠òÊúçÂä°Á´Ø‰∏ä‰∏ãÊñá
      serverContext = s;
    },
    async transform(this: PluginContext, code: string, id: string) {
      // Âè™Â§ÑÁêÜ JS Áõ∏ÂÖ≥ÁöÑËØ∑Ê±Ç
      if (!isJSRequest(id)) {
        return null;
      }
      await init;
      // Ëß£Êûê import ËØ≠Âè•
      const [imports] = parse(code);
      const ms = new MagicString(code);

      const { moduleGraph } = serverContext;
      const curMod = moduleGraph.getModuleById(id)!;
      const importedModules = new Set<string>();

      // ÂØπÊØè‰∏Ä‰∏™ import ËØ≠Âè•‰æùÊ¨°ËøõË°åÂàÜÊûê
      for (const importInfo of imports) {
        // ‰∏æ‰æãËØ¥Êòé: const str = `import React from 'react'`
        // str.slice(s, e) => 'react'
        const { s: modStart, e: modEnd, n: modSource } = importInfo;
        if (!modSource) continue;
        // ÈùôÊÄÅËµÑÊ∫ê
        if (modSource.endsWith(".svg")) {
          // Âä†‰∏ä ?import ÂêéÁºÄ
          const resolvedUrl = path.join(path.dirname(id), modSource);
          ms.overwrite(modStart, modEnd, `${resolvedUrl}?import`);
          continue;
        }
        // Á¨¨‰∏âÊñπÂ∫ì: Ë∑ØÂæÑÈáçÂÜôÂà∞È¢ÑÊûÑÂª∫‰∫ßÁâ©ÁöÑË∑ØÂæÑ
        if (BARE_IMPORT_RE.test(modSource)) {
          const bundlePath = normalizePath(
            path.join("/", PRE_BUNDLE_DIR, `${modSource}.js`)
          );
          ms.overwrite(modStart, modEnd, bundlePath);
          importedModules.add(bundlePath);
        } else if (modSource.startsWith(".") || modSource.startsWith("/")) {
          // Áõ¥Êé•Ë∞ÉÁî®Êèí‰ª∂‰∏ä‰∏ãÊñáÁöÑ resolve ÊñπÊ≥ïÔºå‰ºöËá™Âä®ÁªèËøáË∑ØÂæÑËß£ÊûêÊèí‰ª∂ÁöÑÂ§ÑÁêÜ
          const resolved = await this.resolve(modSource, id);
          if (resolved) {
            ms.overwrite(modStart, modEnd, resolved.id);
            importedModules.add(resolved.id);
          }
        }
      }
      moduleGraph.updateModuleInfo(curMod, importedModules);

      return {
        code: ms.toString(),
        // ÁîüÊàê SourceMap
        map: ms.generateMap(),
      };
    },
  };
}
